// В этом файле должны быть стили для БЭМ-блока form, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, $media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

$font-family--headings:       -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif !default;

$font-size--h3:               1.5em !default;

$line-height:                 1.375em !default;

.form {

  &__item {
    display: flex;
    width: 100%;
  }

  &__input {
    width: 100%;
    height: 60px;
    padding: 22px 20px; 
    border: 0;

    color: $gray;
    font-size: 18px;

    border-radius: 4px;
    background-color: $color-default;
  }

  &__submit {
    display: flex;
    max-width: none;
    width: 100%;
    height: 70px;

    font-size: 16px;
    font-weight: 700;
  }

  &:not(:last-child) {
    margin-bottom: $line-height;
  }

  fieldset {
    min-width: 0;
    padding: 0;
    margin: 0;
    border: 0;

    &:not(:last-child) {
      margin-bottom: $line-height;
    }
  }

  legend {
    display: block;
    width: 100%;
    font-size: $font-size--h3;
    font-family: $font-family--headings;
    font-weight: 700;
    line-height: $line-height;
    color: currentColor;
    margin-top: $line-height;
    margin-bottom: ($line-height / 2);
  }
}
