 // В этом файле должны быть стили для БЭМ-блока callback, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.callback {
	display: flex;
	flex-direction: column;
	padding: 146px 0 250px;

	background-color: $gray-lightest--bright;
	background-image: url("../img/bg-callback.png");
	background-position: 0 bottom;
	background-repeat: no-repeat;
	background-size: contain;

	&__container {
		max-width: 1170px;
		width: 100%;
		padding: 0 15px;
		margin: 0 auto;	  
	}

	&__title {
		margin: 0;
		margin-bottom: 25px;
	}

	&__descr {
		margin: 0;
		margin-bottom: 30px;

	  color: $color-text-sub;
	  font-size: 16px;
	  line-height: 26px;
	}

	&__form {
		display: flex;
		height: 62px;

	  border-radius: 4px;
	  overflow: hidden;
	}

	&__input {
		display: flex;
		align-items: center;
		width: 300px;
		height: 100%;
		padding: 0 20px;
		border: none;

		color: $gray;
		font-size: 16px;

		&:focus {
			outline-color: $color-action;
		}
	}
	
	&__input-wrap {
	  position: relative;

		&::after {
			content: "";

			position: absolute;
			top: 14px;
			bottom: 14px;
			right: 0;

			width: 1px;

			background-color: $gray-lighten;
		}
	}

	&__submit {
		max-width: 225px;
		height: 100%;

		font-size: 16px;
		font-weight: 700;
	}

	&__small {
		color: $color-action;
		font-size: 14px;
	}

	&__small-icon {
		display: inline;
	  width: 14px;
	  height: 16px;
	}
}
