// В этом файле должны быть стили для БЭМ-блока about-us, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.about-us {
	min-height: 667px;

	background-image: url("../img/bg-about-us.jpg");
	background-position: -110px center;
	background-size: contain;

	&__container {
		position: relative;

		display: flex;
		max-width: 1170px;
		width: 100%;
		
		padding: 204px 15px 0;
		margin: 0 auto;
	}

	&__content {
	  max-width: 350px;
	  width: 100%;
	}

	&__gallery {
		position: absolute;
		top: -36px;
		right: -214px;

		padding: 0 0 90px 90px;
	}

	&__title {
		margin: 0;
		margin-bottom: 30px;
	}

	&__descr {
		max-width: 350px;
		width: 100%;
	  margin: 0;
	  margin-bottom: 30px;
	  
	  color: $gray-light;
	  font-size: 14px;
	  line-height: 21px;
	}

	&__gallery-item {
		&_size_large {
			width: 800px;
			height: 570px;
		}


		&_size_small {
			position: absolute;
			bottom: 0;
			left: 0;

		  width: 350px;
		  height: 350px;
		}
	}
}
