 // В этом файле должны быть стили для БЭМ-блока gallery, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.gallery {
	padding: 71px 0 79px;

	&__container {
		max-width: 1170px;
		width: 100%;
		padding: 0 15px;
		margin: 0 auto;
	}

	&__title {
	  margin-bottom: 72px;
	}

	&__items {
		display: flex;
	  margin-right: -15px;
	  margin-left: -15px;
	}
	
	&__items-col {
	  display: flex;
	  flex-direction: column;
	}

	&__item {
		margin: 0 15px;
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	  
	  &_width_small {
	    width: 263px;
	  }

	  &_width_middle {
	    width: 361px;
	  }

	  &_width_big {
	    width: 455px;
	  }
	}
}
