 // В этом файле должны быть стили для БЭМ-блока newsletter, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.newsletter {
	position: relative;

	max-width: 515px;
	padding: 44px 30px 50px;
	margin: 0 auto 160px;
	
	overflow: hidden;
	border-radius: 8px;

	&__background-layer-1 {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 0;

		background-image: url("../img/bg-newsletter.jpg");
		background-size: cover;
	}
	
	&__background-layer-2 {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;

	  background-color: fade-out($color-default, .80);
	}

	&__container {
		position: relative;
		z-index: 2;
	}

	&__title {
		display: flex;
		justify-content: center;
		width: 100%;
		margin: 0;
		margin-bottom: 20px;
		
		color: $color-action;
		font-size: 36px;
		text-align: center;
		font-weight: 700;
		text-transform: uppercase;
	}

	&__descr {
	  margin: 0;
	  margin-bottom: 34px;
	  
	  color: $color-text;
	  font-size: 16px;
	  text-align: center;
	}

	&__form-item {
		margin-bottom: 20px;
	}
}
