// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;

	display: flex;
	align-items: center;
	height: 88px;
	padding: 20px 0;

	background-color: fade-out($color-default, .5);

	transition-timing-function: $transition-type;
	transition-duration: $transition-time;

	&.fixed {
		height: 58px;
		padding: 0;

		background-color: $color-default;
	}

	&__container {
		display: flex;
		align-items: center;
		max-width: 1170px;
		width: 100%;
		padding: 0 15px;
		margin: 0 auto;
	}

	&__logo {
		margin-right: 60px;
	}

	&__page-nav {
		margin-right: auto;
	}
}
