// В этом файле должны быть стили для БЭМ-блока page-nav, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-nav {
	display: flex;
	align-items: center;
	color: $gray-light;
	font-size: 12px;
	font-weight: 700;
	text-transform: uppercase;	

	&__item {
		@include reset-link;

		position: relative;
		
		&::before {
			content: '';
			
			position: absolute;
			bottom: -6px;
			left: 0;
			right: 0;

			height: 2px;

			background-color: darken($color-action, 4);
			opacity: 0;

			transition-timing-function: $transition-type;
			transition-duration: $transition-time;
		}

		&:hover,
		&.current {
			&::before {
			  opacity: 1;

			  transition-timing-function: $transition-type;
			  transition-duration: $transition-time;
			}
		}

		&:active {
			&::before {
				transform: translateY(-50%) scaleY(1.5);
			}
		}

	  &:not(:last-child) {
	  	margin-right: 20px;
	  }
	}
}
