 // В этом файле должны быть стили для БЭМ-блока steps, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.steps {
	&__container {
		max-width: 1170px;
		width: 100%;
		padding: 71px 15px 55px;
		margin: 0 auto;
	}

	&__title {
		margin-bottom: 75px;
	}

	&__items {
		position: relative;

	  display: flex;
	  justify-content: space-between;

	  &::before {
	  	content: '';
	  
	    position: absolute;
	    top: 24px;
	    left: 100px;
	    right: 100px;
	    z-index: -1;

	    height: 1px;

	    background-image: repeating-linear-gradient(90deg, $gray-lightest--brighten 15px, $gray-lightest--brighten 30px, transparent 30px, transparent 45px);
	  }	
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		max-width: 205px;
		margin: 0 10px;

		&:first-of-type {
			margin: 0;
		}
		
		&:last-of-type {
			margin-right: 0;
		}
	}

	&__number {
	  display: flex;
	  align-items: center;
	  justify-content: center;
	  width: 48px;
	  height: 48px;
	  border: 3px solid $color-action;
	  margin-bottom: 58px;

	  color: $color-action;
	  font-size: 16px;
	  font-weight: 900;

		background-color: $color-default;
	  border-radius: 50%;
	}

	&__icon-wrap {
	  display: flex;
	  align-items: center;
	  justify-content: center;
	  width: 179px;
	  height: 179px;
	  margin-bottom: 29px;

		background-color: $gray-lightest--brighten;
	  border-radius: 50%;

	  background-image: url("../img/bg-steps__hexagon.png");
	  background-repeat: no-repeat;
	  background-position: 27px 27px;
	}
	
	&__icon {
	  &_document {
	  	width: 32px;
	  	height: 40px;
	  }

	  &_person {
	    width: 40px;
	    height: 40px;
	  }

	  &_book {
	    width: 38px;
	    height: 38px;
	  }

	  &_check {
	    width: 38px;
	    height: 38px;
	  }
	}

	&__item-title {
		display: flex;
		justify-content: center;
		margin: 0;
		margin-bottom: 13px;

		font-size: 18px;
		font-weight: 900;
	  text-transform: normal;
	  text-align: center;
	}

	&__item-descr {
		margin: 0;

	  color: $color-text-sub;
	  font-size: 16px;
	  line-height: 22px;
	  font-weight: 300;
	  text-align: center;
	}
}
