// В этом файле должны быть стили для БЭМ-блока brands, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.brands {
	padding: 75px 0;

	background-color: $gray-lightest--bright;

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 1170px;
		width: 100%;
		padding: 0 15px;
		margin: 0 auto;
	}

	&__item {
		margin: 0 10px;

		&:first-of-type {
			margin-left: 0;
		}
		
		&:last-of-type {
			margin-right: 0;
		}
	}
}
