.target {
	@include reset-target;

	position: relative;
	z-index: 1;
	
	display: flex;
	align-items: center;
	max-width: 130px;
	width: 100%;
	height: 40px;

	font-family: $font-family;
	color: $white;
	font-size: 12px;
	font-weight: 700;
	text-transform: uppercase;

	border-radius: 4px;
	background-color: $color-action;
	outline: none;
	overflow: hidden;
	cursor: pointer;

	transition-timing-function: $transition-type;
	transition-duration: $transition-time;

  &__text {
  	position: relative;
  	z-index: 3;

		margin: 0 auto;
  }

 	&::after {
		content: '';

		position: absolute;
		z-index: 2;

		transition-timing-function: $transition-type;
		transition-duration: $transition-time;
	}

	&::after {
		top: 0;
		left: 0;
		right: 0;

		height: 100%;

		background: $color-default;
		opacity: 0;
		transform: translateY(-100%);
		border-radius: 4px;
	}

	&:hover,
	&:active {
		color: $color-action;
	}

	&:hover::after {
		opacity: 1;
		transform: translateY(-20%);
	}

	&:active::after {
		opacity: 1;
		transform: translateY(0%);
	}

  &_background_empty {
		color: $color-action;

  	background-color: $transparent;
  	box-shadow: inset 0 0 0 3px $color-action;

  	&:hover {
  		color: $color-default;
  	}

  	&::after {
  		background-color: $color-action;
  	}
  }
}