@mixin reset-link {
	color: inherit;
	text-decoration: none;
}

@mixin reset-btn {
	border: none;

	background-color: transparent;
	border-radius: 0;
}

@mixin reset-target {
	border: none;

	color: inherit;
	text-decoration: none;

	background-color: transparent;
	border-radius: 0;
}