// В этом файле должны быть стили для БЭМ-блока journal, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

@mixin angle($side) {
	position: absolute;
	#{$side}: 0;

	border: 10px solid transparent;

	@if ($side == left) {
		top: 55px;

		border-right-color: $color-default;

		transform: translate(-100%, 0);
	}

	@if ($side == right) {
		top: 55px;

		border-left-color: $color-default;

		transform: translate(100%, 0);
	}

	@if ($side == top) {
		left: 55px;

		border-bottom-color: $color-default;

		transform: translate(0, -100%);
	}
}

.journal {
	max-width: 1170px;
	width: 100%;
	padding: 71px 15px 90px;
	margin: 0 auto;

	&__title {
		margin: 0 auto;
		margin-bottom: 71px;
	}

	&__notes {
		display: flex;
		max-width: 1140px;
		width: 100%;

		box-shadow: $shadow-journal__notes;
	}

	&__notes-group {
		display: flex;
		flex-direction: column;
	}

	&__note {
		display: flex;

		&_vertical {
			flex-direction: column;
		}
	}

	&__note-preview,
	&__note-content {
		width: 380px;
		height: 380px;
	}

	&__note-preview {
		position: relative;

		display: flex;
		align-items: center;
		justify-content: center;

		background-color: gray;
		overflow: hidden;
	}

	&__note-preview-img {
		width: 380px;
		height: 380px;
	}

	&__note-icon {
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);

		&_heart {
			width: 82px;
			height: 82px;
		}

		&_water {
			width: 61px;
			height: 74px;
		}

		&_plant {
			width: 65px;
			height: 65px;
		}
	}

	&__note-content {
		position: relative;

		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 47px 0;

		margin: 0;

		&_side_right {
			&::before {
				content: "";

				@include angle(left);
			}
		}

		&_side_left {
			order: -1;

			&::before {
				content: "";
				
				@include angle(right);
			}
		}

		&_side_bottom {
			&::before {
				content: "";
				
				@include angle(top);
			}
		}
	}

	&__note-theme {
		margin-bottom: 55px;
		
		color: $color-action;
		font-size: 12px;
		line-height: 1em;
		text-align: center;
		font-weight: 700;
		text-transform: uppercase;
	}

	&__note-title {
		display: flex;
		height: 63px;
		padding: 0 30px;
		margin: 0;
		margin-bottom: 27px;

		font-size: 17px;
		line-height: 20px;
		text-align: center;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.7px;

		overflow: hidden;
	}

	&__note-descr {
		display: flex;
		height: 84px;
		padding: 0 55px;
		margin: 0;
		margin-bottom: 39px;
		
		color: $color-text-sub;
		font-size: 15px;
		line-height: 24px;
		text-align: center;

		overflow: hidden;
	}

	&__note-page {
		margin: 0;
		
		font-size: 15px;
		line-height: 18px;
		font-style: italic;
	}
}
