// В этом файле должны быть стили для БЭМ-блока card, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.card {
	position: relative;

	width: 200px;
	min-height: 380px;
	padding: 15px 20px 60px;

	color: $white;

	background-color: $gray;
	border-radius: 4px;

	&_carpatian {
		background-image: url("../img/bg-card__carpatian.jpg");
	}
	
	&_alps {
	  background-image: url("../img/bg-card__alps.jpg");
	}

	&_kavkaz {
	  background-image: url("../img/bg-card__kavkaz.jpg");
	}

	&_pyrenes {
	  background-image: url("../img/bg-card__pyrenes.jpg");
	}

	&_rocky {
	  background-image: url("../img/bg-card__rocky.jpg");
	}

	&__score {
	  display: flex;
	  margin-bottom: 120px;
	}

	&__score-item {
		width: 16px;
		height: 16px;

		&:not(:last-child) {
			margin-right: 5px;
		}
	}

	&__title {
	  margin: 0;
	  margin-bottom: 25px;
	  
	  color: $white;
	  font-size: 28px;
	  font-weight: 400;
	  text-transform: none;
	}

	
	&__short-descr {
	  margin: 0;

	  font-size: 15px;
	  line-height: 22px;
	}

	&__prices {
	  position: absolute;
	  bottom: 0;
	  left: 0;
	  right: 0;

		display: flex;
		align-items: center;
		justify-content: space-between;
	  padding: 11px 20px;
	}

	&__price {
		display: flex;

		&_type_old {
			font-size: 18px;
			text-decoration: line-through;
		}

		&_type_current {
			font-size: 26px;
			font-weight: 700;

			.card__price-currency {
				order: -1;
			}
		}
	}
}
