// В этом файле должны быть стили для БЭМ-блока reviews, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.reviews {
	max-width: 1170px;
	width: 100%;
	padding: 70px 15px 0;
	margin: 0 auto 140px;

	&__title {

		margin-bottom: 90px;
	}

	&__items {
	  display: flex;
	  justify-content: space-between;
	}
	
	&__item {
	  max-width: 530px;
	  width: 100%;
	  margin: 0;
	  margin-right: 55px;
	}

	&__quote-icon {
	  width: 22px;
	  height: 24px;
	}

	&__quote {
	  margin: 20px 0 30px;

	  color: $gray-lighten;
	  font-size: 18px;
	  line-height: 30px;
	}
	
	&__author {
	  display: flex;
	  align-items: center;
	}

	&__author-avatar {
		display: flex;
	  width: 64px;
	  height: 64px;
	  margin-right: 20px;

	  border-radius: 50%;
		background-color: $gray-lightest;
	}

	&__author-text {
	  color: $color-action;
	  font-size: 16px;
	  font-style: normal;
	}


	&__author-name {
		display: inline;

	  font-weight: 700;
	}

	&__author-profession {
		display: inline;

		font-weight: 300;	  
	}
}
