body {
	box-sizing: border-box;
	
	font-family: $font-family;
	color: $color-text;
}

* {
	box-sizing: inherit;
}

.container {
	max-width: 1200px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;

	&--holder {
		display: flex;
	}
}

.icon {
	display: flex;
}

.visualy-hidden {
	position: absolute;
	opacity: 0;
}

.dh {
	display: none;
}