// В этом файле должны быть стили для БЭМ-блока page-footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-footer {
	color: $color-default;

	&__container {
		display: flex;
		align-items: center;
		justify-content: space-between;
	  max-width: 1170px;
	  width: 100%;
	  height: 83px;
	  padding: 0 15px;
	  margin: 0 auto;
	}

	&__side {
		width: 250px;
	}

	&__phone {
		@include reset-link;

		font-size: 27px;
		font-weight: 900;
	}

	&__copyright,
	&__phone {
		color: lighten($color-action, 20);
	}
}
